@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Zen+Loop&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@700&display=swap);
body {
  margin: 0;
  font-family: "Nunito";
  overflow-x: hidden;
}

.About {
  text-align: center;
  height: 81vh;
}

.plantMoms {
  display: flex;
  margin: auto;
  flex-direction: row;
  justify-content: space-around;
  text-align: center;
  padding: 10px;
  width: 60%;
}

.mom {
  width: 40%;
}

/* RESOURCES */

.Resources {
  text-align: center;
  width: 98%;
  margin: auto;
}

.blog-links {
  display: flex;
  flex-direction: column;
}

.blog-imgs {
  height: 200px;
  width: 200px;
}

a {
  text-decoration: none;
  color: inherit;
}

.videos-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

iframe {
  padding: 5px;
}

.videos-list > div > h3 {
  margin-bottom: 0;
}

/* FORUM */

/* PLANTS */
main {
  margin: auto;
  width: 95%;
  line-height: 0;
  -webkit-column-count: 3;
          column-count: 3;
  grid-column-gap: 3px;
  -webkit-column-gap: 3px;
          column-gap: 3px;
}

.plant-img-main {
  height: auto;
  width: 100%;
  margin: 2px;
  position: relative;
  margin-top: -24px;
  bottom: 0px;
}

.hover-div {
  text-align: center;
  display: none;
  position: relative;
  bottom: 100px;
  z-index: 2;
  margin-bottom: -25px;
  overflow-y: hidden;
}

.plant-img-main:hover + .hover-div {
  display: block;
  overflow-y: hidden;
}

.plant-img-main:hover {
  opacity: 0.5;
  /* cursor: pointer; */
  overflow-y: hidden;
}

/* LOG IN PAGE */

.Auth {
  width: 200px;
  padding: 60px;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-top: 119px;
  color: green;
}

.log-in {
  background-image: url(/static/media/loginBG.197b7dfd.jpeg);
  background-repeat: no-repeat;
  background-size: auto;
  background-position: top;
  height: 83vh;
}

.Footer {
  position: -webkit-sticky;
  position: sticky;
  height: 45px;
  background: #eee;
  margin: 0;
}

footer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 400px;
  margin: auto;
  height: 100%;
  align-items: center;
}

footer a:hover {
  color: rgb(104, 141, 12);
  font-weight: bolder;
}
/* PROFILE PAGE */

.welcome-profile {
  display: flex;
  justify-content: space-around;
  align-items: center;
  /* background-color: #618b4a80; */
  margin: 50px;
  border-radius: 5px;
}

.welcome-banner {
  font-size: 20px;
  font-weight: bold;
  height: 80px;
  background-color: #618b4a80;
}

.welcome-profile button {
  height: 20px;
  border-radius: 5px;
}

/* plant details */
.all-details {
  border: 2px solid black;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 80%;
  margin: 2em auto;
  align-items: center;
  max-width: 750px;
}

.plant-img-profile {
  height: 225px;
  padding: 2em;
}

.plant-title-profile {
  display: flex;
  flex-direction: column;
  max-width: 250px;
}
.plant-title-profile span {
  display: flex;
  flex-direction: column;
  text-align: center;
}

/* PLANT MAINTENANCE */
.plant-care {
  display: flex;
  flex-direction: row;
}

.plantcare-box {
  border: 1px solid #333333;
  width: 300px;
  height: 200px;
  overflow: scroll;
  margin: auto;
  margin-bottom: 2em;
  background-color: white;
}

/* LIKE BUTTON */
.like-btn {
  position: relative;
  outline: none;
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  top: 10px;
  left: 5px;
  z-index: 1;
}

.openPlant {
  width: 100%;
  z-index: 100;
}

.openPlant:after {
  display: contents;
  padding: 0;
  z-index: 100;
}

.like-btn img {
  display: block;
  height: auto;
}

/* REMOVE BUTTON */
.remove-icon {
  position: relative;
  outline: none;
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
}

/* ADD PLANT */

.add-plant-form {
  display: flex;
  flex-direction: column;
  padding: 20px;
  margin: auto;
  justify-content: center;
  text-align: center;
  width: 200px;
  font-size: 18px;
  font-weight: 800;
}
.add-plant {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 150px;
  margin: auto;
}
.add-plant option {
  text-align: center;
  justify-content: center;
}

/* COMMENT BOX */
.comment-box {
  width: 50%;
  height: 80px;
  margin: 20px auto;
  font-size: 15px;
  display: flex;
  justify-content: center;
  font-family: "Nunito";
  outline: none;
  background: #eee;
}

.post-box {
  width: 90%;
  height: 80px;
  margin: auto;
  font-size: 15px;
  display: flex;
  justify-content: center;
  font-family: "Nunito";
}

.view-all-comments-btn {
  outline: none;
  font-size: 15px;
  background-color: Transparent;
  border: black 1px solid;
  cursor: pointer;
  overflow: hidden;
  margin: auto;
  display: flex;
  align-items: flex-end !important;
  font-family: "Nunito";
}

.view-all-comments-btn:hover {
  background: #eee;
  cursor: pointer;
}

.eachPost h2 {
  font-family: "Nunito Sans";
  font-weight: 700;
}
/* FORUM COMMENT MODAL */

.forum-modal-comments {
  list-style-type: none;
}

.ReactModal__Content--after-open {
  z-index: 100 !important;
}

/* MEDIA QUERIES */

@media screen and (max-width: 700px) {
  main {
    -webkit-column-count: 2;
            column-count: 2;
  }
  .videos-list {
    display: flex;
    flex-direction: column;
  }
  .all-details {
    flex-direction: column;
    text-align: center;
  }
  .plant-img-profile {
    padding-bottom: 0px;
  }

  .plant-care {
    display: flex;
    flex-direction: column;
  }

  .plantMoms {
    display: flex;
    flex-direction: column;
  }
}

@media screen and (min-width: 1000px) {
  main {
    -webkit-column-count: 4;
            column-count: 4;
  }
  .colums {
    display: flex;
    flex-wrap: wrap;
  }
}

@media screen and (min-width: 1800px) {
  main {
    -webkit-column-count: 5;
            column-count: 5;
  }
}

/***** nav-bar *****/
body {
  overflow-x: hidden;
}
.nav-bar {
  display: flex;
  justify-content: space-between;
  padding-right: 2em;
  font-size: 18px;
  width: 100%;
}

.menu li:hover {
  text-decoration: underline;
}

.nav-bar h1 {
  padding-left: 1em;
  color: black;
  font-size: 42px;
  font-family: "Zen Loop", cursive;
  display: flex;
}
.logo {
  width: 200px;
}
.nav-bar ul {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}

.nav-bar li {
  color: black;
  padding: 15px;
  display: flex;
  align-content: flex-end;
  flex-wrap: wrap;
  font-family: "Zen Loop", cursive;
  font-size: 28px;
  font-weight: 600;
}

.menu {
  display: flex;
  flex-direction: row;
  list-style: none;
  justify-content: flex-end;
  font-size: 22px;
  margin-bottom: 0;
}

.menu .link {
  text-decoration: none;
  padding: 10px;
  display: flex;
  align-content: flex-end;
  color: black;
  padding-right: 30px;
}

.menu .link:hover {
  color: black;
}
.input-hidden {
  display: none;
}

/* HAMBURGER MENU */
@media screen and (max-width: 787px) {
  .link {
    text-decoration: none !important;
    transition: color 0.3s ease;
  }
  .menuToggle {
    display: block;
    position: absolute;
    top: 35px;
    right: 50px;
    z-index: 2;
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
  }
  .menuToggle .input-hidden {
    display: block;
    width: 40px;
    height: 32px;
    position: absolute;
    top: -7px;
    left: -5px;
    cursor: pointer;
    opacity: 0;
    z-index: 2;
    -webkit-touch-callout: none;
  }

  .menuToggle span {
    display: block;
    width: 33px;
    height: 4px;
    margin-bottom: 5px;
    position: relative;
    background: black;
    border-radius: 3px;
    z-index: 1;
    -webkit-transform-origin: 4px 0px;
            transform-origin: 4px 0px;
    transition: background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease,
      -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
      background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
      background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease,
      -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  }

  .menuToggle span:first-child {
    -webkit-transform-origin: 0% 0%;
            transform-origin: 0% 0%;
  }

  .menuToggle span:nth-last-child(2) {
    -webkit-transform-origin: 0% 100%;
            transform-origin: 0% 100%;
  }

  .menuToggle .input-hidden:checked ~ span {
    opacity: 1;
    -webkit-transform: rotate(45deg) translate(-2px, -1px);
            transform: rotate(45deg) translate(-2px, -1px);
    background: #232323;
  }

  .menuToggle .input-hidden:checked ~ span:nth-last-child(3) {
    opacity: 0;
    -webkit-transform: rotate(0deg) scale(0.2, 0.2);
            transform: rotate(0deg) scale(0.2, 0.2);
  }

  .menuToggle .input-hidden:checked ~ span:nth-last-child(2) {
    opacity: 1;
    -webkit-transform: rotate(-45deg) translate(0, -1px);
            transform: rotate(-45deg) translate(0, -1px);
  }

  .menu {
    position: absolute;
    width: 200px;
    margin: -100px 0 0 0;
    padding: 50px;
    padding-top: 125px;
    right: -100px;
    background: white;
    -webkit-font-smoothing: antialiased;
    -webkit-transform-origin: 0% 0%;
            transform-origin: 0% 0%;
    -webkit-transform: translate(100%, 0);
            transform: translate(100%, 0);
    transition: -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  }

  .menu li {
    padding: 10px 0;
    font-size: 22px;
    display: flex;
    justify-content: flex-end;
    padding-right: 50px;
  }

  .menuToggle .input-hidden:checked ~ ul {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    opacity: 1;
  }
}

@media screen and (min-width: 787px) {
  .nav-bar ul {
    width: 730px;
    padding-left: 0;
  }
}

/* SUGGESTION BOX */

.suggestion-results {
  text-align: center;
}

.suggestion-box {
  border: 1px solid #333333;
  width: 200px;
  height: 200px;
  overflow: scroll;
  margin: auto;
  margin-bottom: 2em;
  background-color: #f9f9f9;
}

.container {
  max-width: 400px;
  width: 100%;
  margin: 0 auto;
  position: relative;
}

.suggestion_box textarea,
.suggestion_box button[type="submit"] {
  font-family: "Zen Loop", cursive;
  font-size: 35px;
}

.suggestion_box {
  background: #f9f9f9;
  padding: 25px;
}

.suggestion_box h3 {
  display: block;
  font-size: 30px;
  font-weight: 400;
}

.suggestion_boxh4 {
  margin: 5px 0 15px;
  display: block;
  font-size: 13px;
}

.suggestion_box h5 {
  margin: 5px 0 15px;
  display: block;
  font-size: 10px;
}

.suggestion_box textarea {
  width: 90%;
  border: 1px solid #ccc;
  background: #fff;
  margin: 0 0 5px;
  padding: 10px;
}

.suggestion_box textarea:hover {
  transition: border-color 0.3s ease-in-out;
  border: 1px solid #aaa;
}

.suggestion_box button[type="submit"] {
  cursor: pointer;
  font-family: "Nunito";
  width: 25%;
  border: none;
  margin: 0 0 5px;
  padding: 10px;
  font-size: 15px;
}

.suggestion_box button[type="submit"]:hover {
  transition: background-color 0.3s ease-in-out;
}

.suggestion_box button[type="submit"]:active {
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.5);
}

.suggestion_box input:focus,
.suggestion_box textarea:focus {
  outline: 0;
  border: 1px solid #999;
}
::-webkit-input-placeholder {
  color: #888;
}
:-moz-placeholder {
  color: #888;
}
::-moz-placeholder {
  color: #888;
}
:-ms-input-placeholder {
  color: #888;
}

